import React from 'react'

import { graphql } from 'gatsby'
import Link from 'gatsby-link'
import Layout from "../components/layout"

import Container from 'muicss/lib/react/container';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col';
import Divider from 'muicss/lib/react/divider';
import ArraySort from 'array-sort'
import Button from 'muicss/lib/react/button';


  class IndexPage extends React.Component {

    constructor(props) {
      super(props);
      this.state = {
        edges:this.props.data.allClgListeXlsxRefClg.edges,
        order:'node.nom',
        reverse:false
      };
    this.onOrderNom = this.onOrderNom.bind(this)
    this.onOrderVille = this.onOrderVille.bind(this)
    this.onOrderRne = this.onOrderRne.bind(this)
    this.onOrderEtatPnc = this.onOrderEtatPnc.bind(this)
    this.onOrderVague = this.onOrderVague.bind(this)
    this.onOrderCategorie = this.onOrderVague.bind(this)


    }

  onOrderNom(event){

    event.preventDefault();
    event.stopPropagation();
    var field='node.nom'
    var edges=ArraySort(this.state.edges,field,{reverse:this.state.reverse})
    this.setState({
      edges:edges,
      order:field,
      reverse:!this.state.reverse
    });
  }
  onOrderVille(event){

    event.preventDefault();
    event.stopPropagation();

    var field='node.ville'
    var edges=ArraySort(this.state.edges,field,{reverse:this.state.reverse})
    this.setState({
      edges:edges,
      order:field,
      reverse:!this.state.reverse
    });
  }

  onOrderRne(event){

    event.preventDefault();
    event.stopPropagation();

    var field='node.rne'
    var edges=ArraySort(this.state.edges,field,{reverse:this.state.reverse})
    this.setState({
      edges:edges,
      order:field,
      reverse:!this.state.reverse
    });
  }

  onOrderEtatPnc(event){

    event.preventDefault();
    event.stopPropagation();

    var field='node.etat_pnc'
    var edges=ArraySort(this.state.edges,field,{reverse:this.state.reverse})
    this.setState({
      edges:edges,
      order:field,
      reverse:!this.state.reverse
    });
  }

  onOrderVague(event){

    event.preventDefault();
    event.stopPropagation();

    var field='node.vague_pnc'
    var edges=ArraySort(this.state.edges,field,{reverse:this.state.reverse})
    this.setState({
      edges:edges,
      order:field,
      reverse:!this.state.reverse
    });
  }
  onOrderCategorie(event){

    event.preventDefault();
    event.stopPropagation();

    var field='node.categorie'
    var edges=ArraySort(this.state.edges,field,{reverse:this.state.reverse})
    this.setState({
      edges:edges,
      order:field,
      reverse:!this.state.reverse
    });
  }





  render(){
    var edges=this.state.edges
    const tickets = this.props.data.allExportDeDemandeUtilisateurXlsxSheet1.edges

    return(
  <Layout>
    <Container fluid={true} style={{marginTop:'22px'}}>
      <Row className="mui--text-caption" >

        <Col md="3"><Button variant="flat" size="small" onClick={this.onOrderNom} className="noselect" ref='nom'>College</Button></Col>
        <Col md="3"><Button variant="flat" size="small" onClick={this.onOrderVille} className="noselect" ref='ville'>Ville</Button></Col>
        <Col md="2"><Button variant="flat" size="small" onClick={this.onOrderVague} className="noselect">Vague</Button></Col>
        <Col md="3"><Button variant="flat" size="small" onClick={this.onOrderCategorie} className="noselect">Cat√©gorie</Button></Col>
        <Col md="1"><Button variant="flat" size="small"  className="noselect">Tickets</Button></Col>


      </Row>
      <Divider style={{marginBottom:'22px'}} />
      {edges.map(function(edge, i){
        var info=edge.node
        var tstyle={color:"#2196f3"}
        if(info.etat_pnc!=="PNC"){
          tstyle={color:"#444444"}
        }
        if(info.etat_pnc!=="PNC" && info.vague_pnc!==""){
          tstyle={color:"#f442a4"}
        }
        var nbtkt=tickets.filter( x=> info.rne==x.node.Etablissement_concern___Num_ro_UAI_d__tablissement).length

          return (
      <Row key={i} className="mui--text-caption,mui--align-middle" style={{height:'20px'}} >

        <Col md="3" ><Link to={"/"+info.rne}><span style={tstyle}>{info.nom}</span></Link></Col>
        <Col md="3"><small>{info.ville}</small></Col>
        <Col md="2"><small>{info.vague_pnc}</small></Col>
        <Col md="3"><small>{info.categorie}</small></Col>
        <Col md="1"><Link to={"/"+info.rne+"/tickets"}><span style={tstyle}>{nbtkt}</span></Link></Col>

      </Row>)
    },this)}
    </Container>
  </Layout>


    )
  }
}

export default IndexPage

export const indexQuery = graphql`
  query IndexQuery {
    allClgListeXlsxRefClg(sort: {fields: nom}) {
      edges {
        node {
          nom
          abreviation
          ancien_nom
          canton
          categorie
          date_pnc(formatString: "DD/MM/YY")
          numid
          email_college
          email_gestionnaire
          etat_pnc
          id
          info_ac_nantes
          rne
          tel_college
          titre
          vague_pnc
          ville
        }
      }
    }
    allExportDeDemandeUtilisateurXlsxSheet1 {
      edges {
          node {
            Etablissement_concern___Num_ro_UAI_d__tablissement
            R_f_rence
              }
      }
    }
}
`
